import React from 'react';
import warehouseData from "./data/warehouseData.json"

class Product extends React.Component {
	render() {
		return (
			<div class="product-container">
				<h4 class="product-name"> {this.props.value.product} </h4>
				<div class="product-details">
					<ul>
					<li> Qty: {this.props.value.qty} </li>
					<li> Replenishment: {this.props.value.replenishment} </li>
					</ul>
				</div>
			</div>
		);
	}
}

class Location extends React.Component {
	render() {

		const products_section = () => {
			if (this.props.showStock) {
				return this.props.value.stock.map((product)=>{
							return <Product value={product} />;
							})
			} else {
				return (
					<div class="product-container">
						Num Products: {this.props.value.stock.length} 
					</div>
				)
			}
		}

		return (
			<div class="location-container">
				<div class="location-header">
					<h3> {this.props.value.name} </h3>
				</div>
				<div class="location-contents-container">
					{products_section()}
				</div>
			</div>
		);
	}
}

class Floor extends React.Component {
	render() {
		return (
			<div class="flex-container level-container">
				<div class="fixed level-header">
					<h2> {this.props.value.level} </h2>
				</div>
				<div class="flex-item level-contents-container">
					{this.props.value.locations.map((location)=>{
    					return <Location value={location} showStock={this.props.showStock}/>;
    				})}
				</div>
			</div>
		);
	}
}

class Warehouse extends React.Component {
  constructor(props) {
	super(props);
	this.state = {
		showStock: true,
	};
  }
  render() {
    return (
    	<div class="warehouse-container">
    		<h1 class="warehouse-name"> Warehouse: {warehouseData.label} </h1>
    		<div>
				<input type="checkbox" 
					onChange={() => this.setState({showStock: !this.state.showStock})} 
					defaultChecked={this.state.showStock}/> 
				Show Stock
			</div>
    		<div class="warehouse-contents-container">
    			{warehouseData.row.map((floor)=>{
    				return <Floor value={floor} showStock={this.state.showStock} />;
    			})}
    		</div>
    	</div>
    );
  }
}

export default Warehouse
